
import { NgZone, Component, OnInit, ViewChild, ElementRef, asNativeElements } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { NavparamService } from '../navparam.service';
//import Map from '@arcgis/core/Map';
//import MapView from '@arcgis/core/views/MapView';

import 'leaflet/dist/leaflet.css';
import * as Leaflet from 'leaflet'; 
//import 'leaflet/dist/images/marker-shadow.png';
//import 'leaflet/dist/images/marker-icon.png';
//import 'leaflet/dist/images/marker-icon-2x.png';
import {Map, tileLayer, marker} from 'leaflet';

import "esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css";
import "esri-leaflet-geocoder/dist/esri-leaflet-geocoder";
import * as ELG from 'esri-leaflet-geocoder';


@Component({
  selector: 'app-panel-driver',
  templateUrl: './panel-driver.page.html',
  styleUrls: ['./panel-driver.page.scss'],
})
export class PanelDriverPage implements OnInit {

  map: Leaflet.Map;
  properties = [];

  acc = true;
  dec = false;
  his = false;
  datastorage: any;
  name : any;
  id: any=0;
  uid: any=0;
  bid: any;
  did: any;
  barangay: any;
  orders: any = [];
  aorders: any=[];
  declined: any = [];
  hist: any=[];
  oid: any;
  queuenum: any;
  cqueuenum: any;
  latitude: any = 0; //latitude
  longitude: any = 0; //longitude
  a = true;
  b = true;

  
  constructor(
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private activatedRoute: ActivatedRoute,
    private storage: Storage,
    private navParamService: NavparamService
  ) { }

  options = {
    timeout: 10000, 
    enableHighAccuracy: true, 
    maximumAge: 3600
  }; 
  
  
  ngOnInit() {
    this.getid();
  }  
  
  doRefresh(event) {
    console.log('Begin async operation');
    
    setTimeout(() => {
      console.log('Async operation has ended');
      event.target.complete();
    }, 2000);
  }
  getid(){

        this.storage.get('storage_xxx').then((res)=>{
          console.log(res);
            this.datastorage = res;
            this.name = this.datastorage.first_name;
            this.bid=this.datastorage.barangay_id;
            console.log(this.bid);
            this.uid=this.datastorage.user_id;
            this.name = this.datastorage.first_name+' '+this.datastorage.last_name;
            this.did = this.datastorage.user_id;
            this.barangay=this.datastorage.barangay_name;

      return new Promise(resolve => {
        
        let body = {
          aksi: 'get_orders_driver',
          uid: this.uid,
          bid: this.bid,
          did: this.did,
          name: this.name,
          barangay: this.barangay 
        }
        this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
          if(res.success == true) {
            console.log(res.result);
            console.log(res.queue);
            console.log(res.adata);
            console.log(res.finish);
            console.log(res.cqueue);
            console.log(res.count);
            console.log(res.stat);
            this.cqueuenum=res.cqueue;
            if(res.finish=='Finished'){

            }
            if(res.finish=='Not finished'){

            }
            if(res.queue==""){
              this.queuenum='None';
              this.a=false;
            }else{
              this.queuenum=res.queue;
              this.b=false;
            } 
            for(let datas of res.adata){
              this.aorders.push(datas);
            } 
            if(this.queuenum==this.cqueuenum){
              for(let datas of res.result){
                this.orders.push(datas);
              }  
            }else{
              this.orders=[];
            }
            console.log(this.orders);
            console.log(res.stat);
          } else {
            console.log(res.queue);
            console.log(res.stat);
          }
        });
      });
    }); 
  }
  

  leafletMap() {
    this.map = new Leaflet.Map('map').setView([14.0221, 120.7284], 14);

    Leaflet.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    }).addTo(this.map);

    const markPoint = Leaflet.marker([14.0221, 120.7284]);
    markPoint.bindPopup('<p>Tashi Delek - Bangalore.</p>');
    this.map.addLayer(markPoint);
    
  }

  openOrder(i){
    this.navParamService.setOrderID(i);
    console.log(this.navParamService.getOrderID());
    //this.map.remove();
    this.router.navigate(['/order-detail']);
  }
  orderOpen(i, a){
    this.navParamService.setOrderID(i);
    this.navParamService.setCon(a);
    console.log(this.navParamService.getCon());
    console.log(this.navParamService.getOrderID());
    //this.map.remove();
    this.router.navigate(['/order-detail']);
  }

  openHistory(i){
    this.navParamService.setOrderID(i);
    console.log(this.navParamService.getOrderID());
    //this.map.remove();
    this.router.navigate(['/history-details']);
  }


  accept(){
    this.acc=true;
    this.dec=false;
    this.his=false;
  }
  decline(){
    this.acc=false;
    this.dec=true;
    this.his=false;
  }
  history(){
    this.acc=false;
    this.dec=false;
    this.his=true;
  }
  ionViewWillEnter(){
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.name = this.datastorage.first_name;
        this.bid=this.datastorage.barangay_id;
    return new Promise(resolve => {
      let body = { 
        aksi: 'get_history_driver',
        bid: this.bid
      }
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
          console.log(res.result)
          for(let datas of res.result){
            this.hist.push(datas);
          }   
          console.log(this.hist);
        } else {

        }
      });
    });
  });
  }

  ionViewDidEnter() {

    this.activatedRoute.params.subscribe((data : any)=>{
      this.id = data.id;
      console.log(this.id)
    //this.leafletMap();
    console.log(this.id)
    if(this.id == 1){
      window.location.reload();
    }
    this.navParamService.clear();
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.name = this.datastorage.first_name;
        this.bid=this.datastorage.barangay_id;
         

    });

    return new Promise(resolve => {
      let body = {
        aksi: 'get_decline_driver',
        bid: this.bid
      }
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
          console.log(res.result)
          for(let datas of res.result){
            this.declined.push(datas);
          }   
          console.log(this.orders);
        } else {

        }
      });
    });
  });

  }


  async logout(){
    const loader = await this.loadingController.create({
      message: 'Please wait',
    });
    loader.present();
    loader.dismiss();
    this.storage.clear();
    this.presentToast('Log out successfully');
    this.navController.navigateRoot(['/intro']);
  }

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }  

  async presentAlert(a) {
    const alert = await this.alertController.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Okay',
          handler: () => {
            console.log('Confirm Okay');
          }
        }
      ]
    });
  }

  queue(){

      this.storage.get('storage_xxx').then((res)=>{
          this.datastorage = res;
          this.name = this.datastorage.first_name+' '+this.datastorage.last_name;
          this.did = this.datastorage.user_id;
          this.barangay=this.datastorage.barangay_name;
          this.bid=this.datastorage.barangay_id;
      return new Promise(resolve => {
        let body = { 
          aksi: 'get_queue',
          did: this.did,
          name: this.name,
          barangay: this.barangay,
          bid: this.bid
        }
        console.log(res);
        this.accessProviders.postData(body, 'queue.php').subscribe((res:any) => {
          if(res.success == true) {
            console.log(res.msg)
            this.router.navigate(['/panel-driver/1']);
          } else {
            console.log(res.msg)
          }
        });
      });
    });
  }

}